<template>
<div>
<div v-if="status === 1">
  <div class="progress">
	<div class="progress-bar progress-bar-success" role="progressbar"
		 aria-valuenow="60" aria-valuemin="0" aria-valuemax="100"
		 style="width: 33%;">
		<span></span>
	</div>
	<div class="progress-bar progress-bar-info" role="progressbar"
		 aria-valuenow="60" aria-valuemin="0" aria-valuemax="100"
		 style="width: 33%; margin-bottom: 0px;">
		<span></span>
	</div>
	<!-- <div class="progress-bar progress-bar-warning" role="progressbar"
		 aria-valuenow="60" aria-valuemin="0" aria-valuemax="100"
		 style="width: 30%;">
		<span class="sr-only"></span>
	</div> -->
</div>
<div  data-spy="scroll" data-target="#navbar-example" data-offset="0" style="height: 450px; overflow-x:hidden; position: relative; margin-top: 0px;">
<div class="panel panel-default" >
<p class="text-left text-justify"><br>:<br><br><br><br>
        <br><br>
        <table class="table table-bordered">
	
	<thead>
		<tr>
			<th></th>
			<th></th>
		</tr>
	</thead>
	<tbody>
		<tr v-for="aclass in classes">
			<td>{{aclass.class_name}}</td>
			<td><label class="radio-inline">
		<input type="radio" name="optionsRadiosinline" id="optionsRadios3" :value="'1'+aclass.class_id" v-model="picked"> 
	</label>
	
	<label class="radio-inline">
		<input type="radio" name="optionsRadiosinline" id="optionsRadios4"  :value="'2'+aclass.class_id" v-model="picked"> 
	</label>
	<label class="radio-inline">
		<input type="radio" name="optionsRadiosinline" id="optionsRadios4"  :value="'3'+aclass.class_id" v-model="picked"> 
	</label></td>
		</tr>
		</tbody>
		</table>
</p>

</div>
<div class="panel panel-default" style="width: 1000px; text-align: center;" v-if="picked === 0">
<h3></h3>
<div class="panel panel-default" style="width: 800px;margin-left: 100px;" v-for="group in groupData">
	<div class="panel-heading">
		<h3 class="panel-title">
			{{group.class_name}}
		</h3>
	</div>

	<table class="table table-bordered">
	<!-- 	<h3 class="panel-title">
			
		</h3> -->

		<thead>
			<tr>
				<th></th>
				<th></th>
				<th></th>
			</tr>
		</thead>
		<tbody>
			<tr v-for="info in group.groupinfo">
				<td>{{info.group_id}}</td>
				<td><tr v-for="member in info.group_membermsg">{{member.school_num}}</tr></td>
				<td><tr v-for="member in info.group_membermsg">{{member.name}}</tr></td></td>
			</tr>
			
		</tbody>
</table>
	
</div>
	
	
</div>  	

<div v-else>
	<div class="panel panel-default" v-for="aclass in classes">
	<div class="panel-heading">
		<h3 class="panel-title">
			{{aclass.class_name}}
		</h3>
	</div>
	<div class="panel-body" v-if="picked === '1'+aclass.class_id">
		<p><button @click="upload(1,aclass.class_id,$event)"></button></p>
	</div>
	<div class="panel-body" v-if="picked === '2'+aclass.class_id">
		<p><button @click="upload(2,aclass.class_id,$event)"></button></p>
	</div>
	<div class="panel-body" v-if="picked === '3'+aclass.class_id">
	<form>
		<p class="text-left" style="float: left;">Excel:</p>&nbsp&nbsp<input style="float: left;" type="file" @change="getFile($event)"> <button style="float: left;" @click="upload(3, aclass.class_id, $event)"></button>
	</form>
	<table class="table table-bordered">
	<tbody>
		<tr>
			<td></td>
			<td></td>
			<td></td>
		</tr>
		<tr>
			<td></td>
			<td>1</td>
			<td>1</td>
		</tr>
		<tr>
			<td></td>
			<td>2</td>
			<td>1</td>
		</tr>
		<tr>
			<td></td>
			<td>3</td>
			<td>2</td>
		</tr>
	</tbody>
	</table>
	</div>
</div>
</div>
</div>
</div>
<div v-if="status === 2">
  <groupList></groupList>
</div>
<div v-if="status === 1">
<div v-if="picked === 0" style="margin-top: 30px;">
	<button class="btn btn-default" @click="saveGroup()"></button>
</div>
<div v-else style="margin-top: 30px;"><!--  -->
	<button class="btn btn-default" @click="getAllGroup(classes)"></button>
</div>
</div>
	</div>
</template>

<script type="text/javascript">
import axios from 'axios'
import store from '@/store.js'
import groupList from '@/components/groupList'
export default {
  data () {
    return {
      classes: [],
      picked: '',
      checkedNames: [],
      select: [],
      file: '',
      choseArr: [],
      a: 0,
      groupData: [],
      status: 1
    }
  },
  components: {
    groupList
  },
  props: ['token', 'course_id'],
  methods: {
    saveGroup: function () {
      console.log('#####this is save group########')
      store.save('groupMsg', this.groupData)
      this.status = 2
    },
    getFile: function (event) {
      this.file = event.target.files[0]
      console.log('file--------------->', this.file)
      console.log('picked---------->', this.picked)
    },
    getGroupData: function (id) {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/teacher/get_groupingmsg',
        method: 'post',
        data: {
          class_id: id,
          type: 'T2005',
          token: self.token
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.groupData.push(res.data.msg)
        console.log('-------------->', self.groupData)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getAllGroup: function (classes) {
      this.picked = 0
      console.log('getAllGroup-------->', classes)
      console.log('class---------->', classes[0].class_id)
      for (var i = 0; i < classes.length; i++) {
        console.log('class_id-------->', classes[i].class_id)
        this.getGroupData(classes[i].class_id)
      }
      //   // this.getGroupData(x.class_id)
      // }
    },
    upload: function (grouptype, id, event) {
      event.preventDefault()
      let self = this
      let formData = new FormData()
      formData.append('groupingtype', grouptype)
      formData.append('class_id', id)
      formData.append('type', 'T2004')
      formData.append('token', self.token)
      formData.append('file', self.file)
      axios({
        url: 'https://diningx.cn/pa/public/api/teacher/grouping',
        method: 'post',
        data: formData,
    //     transformRequest: [function (data) {
    // // Do whatever you want to transform the data
    //       let ret = ''
    //       for (let it in data) {
    //         ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
    //       }
    //       return ret
    //     }],
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(function (res) {
        // console.log(res.data.msg[0].name + 'T2003-----' + res.data.msg.length)
      }).catch(function (err) {
        console.log(err)
      })
      this.file = ''
    },
    getData: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/teacher/get_student_by_course',
        method: 'post',
        data: {
          type: 'T2003',
          token: self.token,
          course_id: self.course_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res.data.msg[0].name + 'T2003-----' + res.data.msg.length)
        self.classes = res.data.msg
        console.log(self.classes)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style type="text/css">
td {
	text-align: center;
}
th {
	text-align: center;
}
tr {
	text-align: center;
}
</style>


